import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"

export default props => {
  const { data } = props
  const siteTitle = data.site.siteMetadata.title

  return (
    <Layout location={props.location} title={siteTitle}>
      <p>
        My name is Jim Vaughan and I am a Lead Software Engineer on the Data Management and Services team. I have over 20 years of 
        experience. I love learning new things.
      </p>

      <h3>Education</h3>
      <p>
        BS in Computer Science from <a href="https://www.mtsu.edu/programs/computer-science/" target="_blank" rel="noopener noreferrer">
          Middle Tennessee State University
              </a>
      </p>

      <p>
        MS in Computer Science from <a href="https://omscs.gatech.edu/" target="_blank" rel="noopener noreferrer">
          Georgia Institute of Technology
              </a> with an emphasis in Interactive Intelligence
          </p>
    </Layout>
  )
}

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
